.ul {
  display: flex;
}
.div {
  margin-bottom: 25px;
}
.table {
  width: 100%;
  margin-bottom: 20px;
}

.theadMain {
  background-color: brown;
}

.theadArchive {
  background-color: rgb(184, 86, 6);
}

.theadMain,
.theadArchive {
  color: white;
  fill: white;
}

.th {
  padding: 15px;
  border: 1px solid #222222;
}

.th:first-child {
  border-top-left-radius: 10px;
}
.th:last-child {
  border-top-right-radius: 10px;
}
.btnContainer {
  display: flex;
  justify-content: flex-end;
}
.btnOpen {
  border: none;
  background-color: rgb(184, 86, 6);
  padding: 10px;
  text-transform: uppercase;
  border-radius: 10px;
  color: white;
  font-weight: 700;
  box-shadow: 2px 2px 4px 0px #000000;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.btnOpen:hover,.btnOpen:focus{
  background-color: rgb(131, 60, 2);
}
.btnOpen:not(:last-child) {
  margin-right: 10px;
}
